:root {
  --purple: hsl(256, 26%, 20%);
  --grayish-blue: hsl(216, 30%, 68%);
  --dark-purple: hsl(270, 9%, 17%);
  --gray: hsl(0, 0%, 98%);
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --grayish-purple: hsl(273, 4%, 51%);
}

body {
  font-size: 1rem;
  margin: 0;
}

h1 {
  margin: 0;
  font-size: 3rem;
  font-weight: 400;
  font-family: "DM Serif Display";
  line-height: 3.4rem;
  text-wrap: wrap;
  width: 22rem;
  padding-bottom: 1rem;
  padding-top: 1.4rem;
}

h1::before {
  content: "";
  margin: 0;
  display: block;
  height: 1.4rem;
  width: 6.5rem;
  background-color: var(--dark-purple);
  border-top: 0.02rem solid var(--white);
}

h2 {
  color: var(--black);
  font-size: 3rem;
  font-weight: 100;
  font-family: "DM Serif Display";
  line-height: 3.4rem;
  width: 22rem;
  padding-bottom: 2rem;
}

h2::before {
  content: "";
  margin: 0;
  display: block;
  height: 1.4rem;
  width: 6.5rem;
  background-color: var(--white);
  border-top: 0.02rem solid var(--grayish-purple);
}

h3 {
  margin: 0;
  color: var(--white);
  font-size: 2.5rem;
  font-weight: 400;
  font-family: "DM Serif Display";
  line-height: 2.5rem;
  width: 22rem;
}

h4 {
  margin: 0;
  padding-top: 2rem;
  padding-bottom: 0.8 rem;
  color: var(--black);
  font-size: 1.4rem;
  font-weight: 400;
  font-family: "DM Serif Display";
}

.random {
  background-color: var(--white);
  padding-top: 3rem;
  padding-left: 10rem;
  padding-right: 10rem;
  padding-bottom: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.random a {
  text-decoration: none;
  color: black;
}
.random a:hover {
  text-decoration: underline;
  color: var(--dark-purple);
  transition: 0.8s ease-in-out;
  cursor: pointer;
}

.button a {
  border: 0.1rem solid var(--purple);
  width: 5.981875rem;
  height: 1.83125rem;
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--purple) !important;
}

.button a:hover {
  background-color: var(--purple);
  color: var(--white) !important;
  transition: 0.3s ease-out;
  cursor: pointer;
}

.buttonb a {
  font-weight: 500;
  text-transform: uppercase;
  width: 5.981875rem;
  height: 1.83125rem;
  border: 0.1rem solid var(--white);
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--white) !important;
}

.buttonb a:hover {
  background-color: var(--white);
  color: var(--purple) !important;
  transition: 0.3s ease-out;
  cursor: pointer;
}

.sniplogo {
  margin: 0;
  width: 4rem;
  height: 4rem;
}

.smlogo {
  fill: var(--purple);
}

nav {
  font-family: "Karla";
  font-size: 0.7rem;
  font-weight: 500;
}

.navmenu {
  display: flex;
  align-items: center;
  gap: 1rem;
  text-transform: uppercase;
}
.navmenu a {
  text-decoration: none;
  color: var(--grayish-purple);
}
.navmenu :hover {
  color: var(--purple);
  transition: 0.3s;
}

.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 10rem;
  padding-right: 10rem;
  min-width: 40rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
}

.section-two {
  display: flex;
  justify-content: space-between;
  background-color: var(--dark-purple);
  color: var(--white);
  font-family: "Karla";
  font-size: 0.67rem;
  font-weight: 100;
  line-height: 1rem;
  height: 25rem;
  padding-left: 10rem;
  padding-right: 10rem;
  padding-top: 3.6rem;
}
.section-two a {
  text-decoration: none;
}

.containern {
  margin: 0;
  width: 23.5rem;
  padding-bottom: 1.5rem;
}

.happy {
  max-width: 23.5rem;
  padding-top: 1.4rem;
  padding-left: 1rem;
}

.section-three {
  background-color: var(--white);
  color: var(--black);
  font-family: "Karla";
  font-size: 0.7rem;
  font-weight: 400;
  padding-left: 10rem;
  padding-right: 10rem;
  padding-top: 7.27375rem;
  padding-bottom: 7.27375rem;
}
.section-three a {
  text-decoration: none;
}

.snippets {
  color: var(--grayish-purple);
  font-family: "Karla";
  font-size: 0.67rem;
  font-weight: 100;
  line-height: 1rem;
  display: flex;
  gap: 2rem;
  padding-bottom: 5rem;
  min-width: 48rem;
}

.snippet {
  min-width: 13.39375rem;
  flex-basis: 100%;
}

.findout {
  background-color: var(--dark-purple);
  color: var(--white);
  min-width: 48rem;
}

.containeri {
  margin: 0 0 0 0;
  padding-left: 3rem;
  padding-right: 3rem;
  padding-top: 3rem;
  padding-bottom: 3rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.section-four {
  background-color: var(--gray);
  font-family: "Karla";
  font-size: 0.67rem;
  font-weight: 500;
  line-height: 1rem;
  padding-left: 10rem;
  padding-right: 10rem;
  padding-bottom: 2rem;
  padding-top: 3rem;
}
.section-four ul {
  list-style-type: none;
}

.top {
  display: flex;
  justify-content: space-between;
  min-width: 48rem;
  padding-bottom: 1.7rem;
  border-bottom: 0.02rem solid var(--grayish-purple);
}

.sm {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
}
.sm a {
  color: var(--grayish-purple);
}
.sm a:hover {
  color: var(--purple);
  cursor: pointer;
  transition: 0.3s ease-in-out;
}

.smlogosvg {
  fill: currentColor;
}

.links {
  padding-top: 1.7rem;
  min-width: 48rem;
  display: flex;
  justify-content: space-between;
  text-transform: uppercase;
  line-height: 1.4rem;
}
.links a {
  text-decoration: none;
  color: var(--purple);
}
.links a:hover {
  text-decoration: underline;
  transition: 0.3s;
}

.title {
  padding-bottom: 1rem;
  color: var(--grayish-purple);
}